import React, { useEffect } from "react";
import GlobalNavigationBar from "@salesforce/design-system-react/components/global-navigation-bar";
import GlobalNavigationBarRegion from "@salesforce/design-system-react/components/global-navigation-bar/region";
import GlobalNavigationBarDropdown from "@salesforce/design-system-react/components/global-navigation-bar/dropdown";
import GlobalNavigationBarLink from "@salesforce/design-system-react/components/global-navigation-bar/link";
import AppLauncher from "@salesforce/design-system-react/components/app-launcher";
import "./app-menu.css";
export default function AppMenu() {
  useEffect(() => {
    document.addEventListener("DOMContentLoaded", function () {
      // make it as accordion for smaller screens
      if (window.innerWidth < 992) {
        // close all inner dropdowns when parent is closed
        document
          .querySelectorAll(".navbar .dropdown")
          .forEach(function (everydropdown) {
            everydropdown.addEventListener("hidden.bs.dropdown", function () {
              // after dropdown is hidden, then find all submenus
              this.querySelectorAll(".submenu").forEach(function (
                everysubmenu
              ) {
                // hide every submenu as well
                everysubmenu.style.display = "none";
              });
            });
          });

        document
          .querySelectorAll(".dropdown-menu a")
          .forEach(function (element) {
            element.addEventListener("click", function (e) {
              let nextEl = this.nextElementSibling;
              if (nextEl && nextEl.classList.contains("submenu")) {
                // prevent opening link if link needs to open dropdown
                e.preventDefault();
                if (nextEl.style.display == "block") {
                  nextEl.style.display = "none";
                } else {
                  nextEl.style.display = "block";
                }
              }
            });
          });
      }
      // end if innerWidth
    });
    document.querySelectorAll(".dropdown-menu a").forEach(function (element) {
      element.addEventListener("click", function (e) {
        let nextEl = this.nextElementSibling;
        if (nextEl && nextEl.classList.contains("submenu")) {
          // prevent opening link if link needs to open dropdown
          e.preventDefault();
          e.stopPropagation();
        }
      });
    });
    // DOMContentLoaded  end
  }, []);
  return (
    <>
      <GlobalNavigationBar>
        <GlobalNavigationBarRegion region="primary">
          <AppLauncher
            ariaHideApp={false}
            children={<></>}
            isOpen={false}
            triggerName="Data Fabric"
          ></AppLauncher>
        </GlobalNavigationBarRegion>
        <GlobalNavigationBarRegion region="secondary" navigation>
          <ul className="slds-grid">
            {/* <li className="slds-context-bar__item slds-is-active">
              <a
                href="#"
                className="slds-context-bar__label-action"
                title="Home"
              >
                <span className="slds-assistive-text">Current Page:</span>
                <span className="slds-truncate" title="Home">
                  Home
                </span>
              </a>
            </li> */}

            <li className="nav-item dropdown slds-context-bar__item">
              <a
                className="nav-link dropdown-toggle slds-context-bar__label-action"
                href="#"
                data-bs-toggle="dropdown"
              >
                Functions
              </a>
              <ul className="dropdown-menu">
                <li>
                  <a className="dropdown-item">Account &raquo;</a>
                  <ul className="submenu dropdown-menu">
                    <li>
                      <a className="dropdown-item" href="#">
                        Account Duplicate Management
                      </a>
                    </li>
                    <li>
                      <a className="dropdown-item" href="#">
                        Account Identification &raquo;
                      </a>
                      <ul className="submenu dropdown-menu">
                        <li>
                          <a className="dropdown-item" href="#">
                            Cloud Marketplace
                          </a>
                        </li>
                        <li>
                          <a className="dropdown-item" href="#">
                            MSP
                          </a>
                        </li>
                      </ul>
                    </li>
                    <li>
                      <a className="dropdown-item" href="#">
                        Global Account Hierarchy Management
                      </a>
                    </li>
                  </ul>
                </li>

                <li>
                  <a className="dropdown-item" href="#">
                    Contact &raquo;
                  </a>
                  <ul className="submenu dropdown-menu">
                    <li>
                      <a className="dropdown-item" href="#">
                        Contact Duplicate Management
                      </a>
                    </li>
                    <li>
                      <a className="dropdown-item" href="#">
                        House Account Contact Cleansing &raquo;
                      </a>
                      <ul className="submenu dropdown-menu">
                        <li>
                          <a className="dropdown-item" href="#">
                            Global House to Regional House
                          </a>
                        </li>
                        <li>
                          <a className="dropdown-item" href="#">
                            Regional House to SFDC Account
                          </a>
                        </li>
                      </ul>
                    </li>
                    <li>
                      <a className="dropdown-item" href="#">
                        Contact Insight Dashboard
                      </a>
                    </li>
                  </ul>
                </li>
              </ul>
            </li>
            <li className="nav-item dropdown " id="myDropdown"></li>
          </ul>
        </GlobalNavigationBarRegion>
      </GlobalNavigationBar>
    </>
  );
}
