import React from "react";

export default function SampleTable() {
  const data = [];

  for (let i = 0; i < 25; i++) {
    data.push(
      <tr class="slds-hint-parent">
        <td class="slds-text-align_center slds-cell_action-mode" role="gridcell">
          <div class="slds-checkbox">
            <input
              type="checkbox"
              name="options"
              tabindex="0"
              aria-labelledby="check-button-label-01 column-group-header"
            />
            <label
              class="slds-checkbox__label"
              for="checkbox-01"
              id="check-button-label-01"
            >
              <span class="slds-checkbox_faux"></span>
              <span class="slds-form-element__label slds-assistive-text">
                Select item 1
              </span>
            </label>
          </div>
        </td>
        <td data-label="Account Name">
          <div class="slds-truncate" title="Cloudhub">
            1234-5678-7878
          </div>
        </td>
        <th data-label="Opportunity Name" scope="row">
          <div class="slds-truncate" title="Cloudhub + Anypoint Connectors">
            <a href="#" tabindex="-1">
              Dummy Account Test
            </a>
          </div>
        </th>
        <td data-label="Close Date">
          <span class="slds-badge slds-theme_success">Open</span>
        </td>
        <td data-label="Prospecting">
          <div class="slds-truncate" title="Prospecting">
            Japan
          </div>
        </td>
        <td data-label="Confidence">
          <div class="slds-truncate" title="20%">
            JP
          </div>
        </td>
        <td data-label="Amount">
          <div class="slds-truncate" title="$25k">
            Tokyo
          </div>
        </td>
        <td data-label="Contact">
          <div class="slds-truncate" title="jrogers@cloudhub.com">
            Chiyoda-ku
          </div>
        </td>
      </tr>
    );
  }

  return (
    <>
      <table
        class="slds-table slds-table_cell-buffer slds-table_bordered"
        aria-label="Example default base table of Opportunities"
      >
        <thead>
          <tr class="slds-line-height_reset">
            <th
              class="slds-text-align_right slds-cell_action-mode"
              scope="col"
              style={{ width: "3.25rem" }}
            >
              <span id="column-group-header" class="slds-assistive-text">
                Choose a row
              </span>
              <div class="slds-th__action slds-th__action_form">
                <div class="slds-checkbox">
                  <input
                    type="checkbox"
                    name="options"
                    id="checkbox-unique-id-297"
                    value="checkbox-unique-id-297"
                    tabindex="0"
                    aria-labelledby="check-select-all-label column-group-header"
                  />
                  <label
                    class="slds-checkbox__label"
                    for="checkbox-unique-id-297"
                    id="check-select-all-label"
                  >
                    <span class="slds-checkbox_faux"></span>
                    <span class="slds-form-element__label slds-assistive-text">
                      Select All
                    </span>
                  </label>
                </div>
              </div>
            </th>
            <th class="" scope="col">
              <div class="slds-truncate" title="Opportunity Name">
                Account ID
              </div>
            </th>
            <th class="" scope="col">
              <div class="slds-truncate" title="Account Name">
                Account Name
              </div>
            </th>
            <th class="" scope="col">
              <div class="slds-truncate" title="Close Date">
                Status
              </div>
            </th>
            <th class="" scope="col">
              <div class="slds-truncate" title="Stage">
                Region
              </div>
            </th>
            <th class="" scope="col">
              <div class="slds-truncate" title="Confidence">
                Country
              </div>
            </th>
            <th class="" scope="col">
              <div class="slds-truncate" title="Amount">
                State
              </div>
            </th>
            <th class="" scope="col">
              <div class="slds-truncate" title="Contact">
                City
              </div>
            </th>
          </tr>
        </thead>
        <tbody>{data}</tbody>
      </table>
    </>
  );
}
