import React from "react";
import Icon from "@salesforce/design-system-react/components/icon";
import PageHeader from "@salesforce/design-system-react/components/page-header";
import AppMenu from "../components/app-menu";
import Footer from "../components/footer";
import AppHeader from "../components/app-header";
import SampleTable from "./sample-table";
import Input from "@salesforce/design-system-react/components/input";
import InputIcon from "@salesforce/design-system-react/components/icon/input-icon";
import Popover from "@salesforce/design-system-react/components/popover";

export default function AccountIdentification() {
  const menu = (
    <div class="slds-form-element">
      <div class="slds-form-element__control">
        <div class="slds-combobox_container">
          <div class="slds-combobox slds-is-open">
            <div class="slds-combobox__form-element slds-input-has-icon slds-input-has-icon_right slds-m-around_small">
              <Input
                iconRight={<InputIcon name="search" category="utility" />}
                placeholder="Keyword"
              />
            </div>
            <div
              class="slds-dropdown_length-with-icon-7 slds-dropdown_fluid"
              role="listbox"
            >
              <ul
                class="slds-listbox slds-listbox_vertical"
                role="group"
                aria-label="My Favorites"
              >
                <li role="presentation" class="slds-listbox__item">
                  <div
                    class="slds-media slds-listbox__option slds-listbox__option_plain slds-media_center"
                    role="presentation"
                  >
                    <h3 class="slds-listbox__option-header" role="presentation">
                      Recent List View
                    </h3>
                  </div>
                </li>
                <li role="presentation" class="slds-listbox__item">
                  <div class="slds-media slds-listbox__option slds-listbox__option_entity slds-listbox__option_has-meta">
                    Unknown Reseller
                  </div>
                </li>
                <li role="presentation" class="slds-listbox__item">
                  <div class="slds-media slds-listbox__option slds-listbox__option_entity slds-listbox__option_has-meta">
                    Unknown Reseller
                  </div>
                </li>
                <li role="presentation" class="slds-listbox__item">
                  <div class="slds-media slds-listbox__option slds-listbox__option_entity slds-listbox__option_has-meta">
                    Unknown Reseller
                  </div>
                </li>
                <li role="presentation" class="slds-listbox__item">
                  <div
                    class="slds-media slds-listbox__option slds-listbox__option_plain slds-media_center"
                    role="presentation"
                  >
                    <h3 class="slds-listbox__option-header" role="presentation">
                      Recent List View
                    </h3>
                  </div>
                </li>
                <li role="presentation" class="slds-listbox__item">
                  <div class="slds-media slds-listbox__option slds-listbox__option_entity slds-listbox__option_has-meta">
                    Unknown Reseller
                  </div>
                </li>
                <li role="presentation" class="slds-listbox__item">
                  <div class="slds-media slds-listbox__option slds-listbox__option_entity slds-listbox__option_has-meta">
                    Unknown Reseller
                  </div>
                </li>
                <li role="presentation" class="slds-listbox__item">
                  <div class="slds-media slds-listbox__option slds-listbox__option_entity slds-listbox__option_has-meta">
                    Unknown Reseller
                  </div>
                </li>
              </ul>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
  return (
    <>
      <AppHeader />
      <AppMenu />
      <div class="">
        <div class="container-fluid p-3">
          <div className="mb-3">
            <PageHeader
              icon={
                <Icon
                  assistiveText={{ label: "User" }}
                  category="standard"
                  name="account"
                />
              }
              onRenderActions={() => <></>}
              title={<>Cloud Marketplace</>}
              variant="object-home"
              trail={[<a>Account</a>, <a>Account Identification</a>]}
            />
          </div>
          {/* <div className="mb-3">
            <div
              class="slds-scoped-notification slds-media slds-media_center slds-scoped-notification_light border"
              role="status"
            >
              <div class="slds-media__figure">
                <span
                  class="slds-icon_container slds-icon-utility-info"
                  title="information"
                >
                  123
                  <span class="slds-assistive-text">information</span>
                </span>
              </div>
              <div class="slds-media__body">
                <p>
                  It looks as if duplicates exist for this lead.
                  <a href="#">View Duplicates</a>
                </p>
              </div>
            </div>
          </div> */}
          <div class="row g-2">
            <div class="col-md-9 ">
              <div className="card h-100 p-3">
                <div class="slds-clearfix mb-3">
                  <div class="slds-float_left d-flex">
                    <div class="slds-text-heading_medium">
                      Unknown End Customers
                    </div>
                    <Popover
                      align="bottom left"
                      hasNoCloseButton={true}
                      body={menu}
                    >
                      <button
                        class="slds-button slds-button_icon slds-button_icon-border-filled ms-3"
                        title="More Options"
                      >
                        <Icon
                          assistiveText={{ label: "User" }}
                          category="utility"
                          name="down"
                          size="x-small"
                        />
                      </button>
                    </Popover>
                  </div>
                  <div class="slds-float_right">
                    <span className="me-3">Record 1-31 of 200</span>
                    <div class="slds-button-group" role="group">
                      <button class="slds-button slds-button_neutral">
                        <Icon
                          assistiveText={{ label: "User" }}
                          category="utility"
                          name="left"
                          size="x-small"
                        />
                      </button>

                      <button class="slds-button slds-button_neutral">
                        <Icon
                          assistiveText={{ label: "User" }}
                          category="utility"
                          name="right"
                          size="x-small"
                        />
                      </button>
                    </div>
                    <button class="slds-button slds-button_neutral ms-3">
                      <Icon
                        assistiveText={{ label: "User" }}
                        category="utility"
                        name="filterList"
                        size="x-small"
                      />
                    </button>
                  </div>
                </div>
                <SampleTable />
              </div>
            </div>
            <div class="col-md-3">
              <div className="card h-100 p-3">
                <h2 className="slds-text-heading_medium mb-3">Search</h2>
                <Input
                  iconRight={<InputIcon name="search" category="utility" />}
                  className="mb-3"
                  placeholder="Keyword"
                />
                <div class="slds-clearfix mb-3">
                  <div class="slds-float_left">
                    <button class="slds-button">Reset</button>
                  </div>
                  <div class="slds-float_right">
                    <button class="slds-button slds-button_brand">
                      Search
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <Footer></Footer>
    </>
  );
}
