import React from "react";
import Icon from "@salesforce/design-system-react/components/icon";
import Avatar from "@salesforce/design-system-react/components/avatar";
import Popover from "@salesforce/design-system-react/components/popover";

export default function AppHeader() {
  const profileContent = (
    <>
      <>
        123
        <br />
        456
      </>
    </>
  );

  const settingContent = (
    <>
      123
      <br />
      456
    </>
  );

  return (
    <>
      <header className="slds-global-header_container">
        <div className="slds-global-header slds-grid slds-grid_align-spread">
          <div className="slds-global-header__item">
            <div className="slds-global-header__logo p-1 d-flex align-items-center">
              <img src="./logo192.png" className="h-100" />
              <span className="mx-2 slds-text-heading_medium">|</span>
              <strong className="slds-text-heading_medium ">Sherlock</strong>
            </div>
            <span className="slds-assistive-text"></span>
          </div>

          <div className="slds-global-header__item">
            <ul className="slds-global-actions">
              <li className="slds-global-actions__item me-3">
                <Popover
                  align="bottom right"
                  hasNoCloseButton={true}
                  body={settingContent}
                  size="small"
                >
                  <a>
                    <Icon
                      assistiveText={{ label: "Warning" }}
                      category="utility"
                      color="warning"
                      name="settings"
                      size="small"
                    />
                  </a>
                </Popover>
              </li>
              <li className="slds-global-actions__item">
                <Popover
                  align="bottom right"
                  hasNoCloseButton={true}
                  body={profileContent}
                >
                  <a>
                    <div className="d-flex align-items-center cursor-pointer">
                      <Avatar
                        size="x-small"
                        assistiveText={{ icon: "User Icon Avatar" }}
                        title="User Icon Avatar"
                        variant="user"
                      />
                      <span className="m-2">John Doe (RD-TW)</span>
                    </div>
                  </a>
                </Popover>
              </li>
            </ul>
          </div>
        </div>
      </header>
      <div style={{ height: "50px" }}></div>
    </>
  );
}
